    /* Layout CSS */

body
{
    font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
    font-size: 14px;
    line-height: 1.428571429;
}

.remove-docs,
html,
body
{
    height: 100%;
}

/* Horizontal navigation */

body.horizontal-menu #page-nav
{
    padding: 0;
}

.main-nav
{
    border-width: 1px 0;
    border-style: solid;
}

.main-nav ul
{
    margin: 0;
    padding: 0;

    list-style: none;
}

.main-nav ul li,
.main-nav ul li a
{
    line-height: 54px;

    position: relative;

    display: block;
    float: left;

    height: 54px;
    font-size: 13px;
}

.main-nav ul li.header {
    text-transform: uppercase;
    color: #333;
    font-weight: bold;
    font-size: 12px;
    padding: 0 20px 10px;
}

.main-nav ul li a
{
    padding: 0 20px;

    border-right: 1px solid transparent;
}

.main-nav ul li a .glyph-icon
{
    margin-right: 5px;

         opacity: .60;

    -moz-opacity: .60;
    filter: alpha(opacity: 60);
}

.main-nav ul li ul
{
    position: absolute;
    z-index: 5;
    top: 100%;
    left: 0;

    display: none;

    min-width: 250px;
    padding: 5px;
}

.main-nav ul li ul li,
.main-nav ul li ul li a
{
    line-height: 30px;

    float: none;

    height: 30px;

    white-space: nowrap;

    border: 0;
}

.main-nav ul li ul li.divider
{
    height: 1px;
}

/* Sidebar */

#header-logo,
#page-sidebar
{
    width: 235px;
}

#sidebar-top
{
    text-align: center;
}

#sidebar-top .nav-justified > li > a
{
    font-size: 18px;

    margin: 0 2px;
    padding: 5px;
}

#sidebar-top .nav-justified
{
    display: block;

    margin: 0;
    padding: 4px;
}

#sidebar-top .nav-pills > li + li
{
    margin: 0;
}

#sidebar-menu li a > .small-badge,
#sidebar-top .nav-pills > li > a .small-badge
{
    position: absolute;
    top: 6px;
    right: 16px;

    width: 8px;
    height: 8px;

    border: 0 !important;
}

#sidebar-menu li a > .small-badge
{
    top: 9px;
    right: auto;
    left: 14px;
}

#sidebar-top .tab-content
{
    padding: 10px;
}

#sidebar-bottom
{
    position: fixed;
    bottom: 0;
    left: 0;

    width: 235px;
    padding: 5px;
}

#page-sidebar
{
    position: fixed;
    z-index: 8;

    height: 100%;
}

/* Sidebar collapsed */

#page-content-wrapper,
#header-logo,
#page-sidebar
{
    -webkit-transition: all .4s ease-in-out;
       -moz-transition: all .4s ease-in-out;
         -o-transition: all .4s ease-in-out;
            transition: all .4s ease-in-out;
}

.sidebar-collapsed #page-sidebar
{
    margin-left: -186px;
}

.sidebar-collapsed #header-logo
{
    margin-left: -186px;
}

.sidebar-collapsed #sidebar-menu li a .glyph-icon
{
    width: 28px;

    text-align: right;
}

.sidebar-collapsed #page-sidebar #page-sidebar-wrapper
{
    overflow-x: hidden;

    width: 50px;
    margin-left: 181px;
}

.sidebar-collapsed #page-header {
    padding-left: 49px;
}

.sidebar-collapsed #page-sidebar #sidebar-menu li a
{
    margin: 0;
}

.sidebar-collapsed #sidebar-files,
.sidebar-collapsed #page-sidebar .divider,
.sidebar-collapsed #sidebar-top,
.sidebar-collapsed #page-sidebar #sidebar-menu li a.sf-with-ul:after,
.sidebar-collapsed #page-sidebar li a span
{
    display: none;
}

.sidebar-collapsed #page-sidebar li.sfHover > ul
{
    display: none !important;
}

.sidebar-collapsed #page-sidebar:hover #page-sidebar-wrapper
{
    width: auto;
    margin-left: 0;
}

.sidebar-collapsed #page-sidebar:hover #sidebar-menu li a.sf-with-ul:after,
.sidebar-collapsed #page-sidebar:hover li a span
{
    display: inline;
}

.sidebar-collapsed #page-sidebar:hover #sidebar-menu li a .glyph-icon
{
    width: 30px;

    text-align: center;
}

.sidebar-collapsed #page-sidebar:hover #sidebar-menu li a
{
    margin: 0 5px;
}

.sidebar-collapsed #page-sidebar:hover
{
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}

.sidebar-collapsed #page-sidebar:hover #sidebar-top
{
    border-top-right-radius: 5px;
}

.sidebar-collapsed #page-sidebar:hover #sidebar-files,
.sidebar-collapsed #page-sidebar:hover #sidebar-top,
.sidebar-collapsed #page-sidebar:hover .divider
{
    display: block;
}

.sidebar-collapsed #page-sidebar:hover li.sfHover > ul
{
    display: block !important;
}

.sidebar-collapsed #page-content-wrapper
{
    margin-left: 50px;
}

.sidebar-collapsed #page-sidebar:hover
{
    margin-left: 0;
}

/* Sidebar menu */

#sidebar-menu li
{
    position: relative;
}

#sidebar-menu li a
{
    font-size: 14px;
    line-height: 42px;

    position: relative;

    display: block;

    height: 42px;
    margin: 0 5px;
    padding: 0 10px;

    text-align: left;
}

#sidebar-menu li a .bs-label
{
    position: absolute;
    top: 13px;
    right: 40px;
}

#sidebar-menu li a.sf-with-ul:after
{
    line-height: 20px;

    position: absolute;
    top: 50%;
    right: 15px;

    width: 20px;
    height: 20px;
    margin-top: -9px;

    -webkit-transition-duration: .5s;
       -moz-transition-duration: .5s;
         -o-transition-duration: .5s;
            transition-duration: .5s;
    -webkit-transition-property: -webkit-transform;
       -moz-transition-property:    -moz-transform;
         -o-transition-property:      -o-transform;
            transition-property:         transform;
}

#page-sidebar li.sfHover > a.sf-with-ul:after
{
    -webkit-transition-duration: .5s;
       -moz-transition-duration: .5s;
         -o-transition-duration: .5s;
            transition-duration: .5s;
    -webkit-transform: rotate(90deg);
       -moz-transform: rotate(90deg);
         -o-transform: rotate(90deg);
            transform: rotate(90deg);
}

#sidebar-menu li ul li a:before,
#sidebar-menu li a .glyph-icon
{
    font-size: 18px;
    line-height: 42px;

    display: block;
    float: left;

    width: 30px;
    height: 42px;
    margin-right: 11px;

    text-align: center;

         opacity: .30;

    -moz-opacity: .30;
    filter: alpha(opacity: 30);
}

#sidebar-menu li a .glyph-icon
{
         opacity: .60;

    -moz-opacity: .60;
    filter: alpha(opacity: 60);
}

#sidebar-menu li ul
{
    display: none;
}

#sidebar-menu li ul li a
{
    font-size: 13px;
    line-height: 30px;

    position: relative;

    height: 30px;
    padding: 0 0 0 30px;
}

#sidebar-menu li ul li a .bs-label
{
    top: 8px;
    right: 10px;
}

#sidebar-menu li ul li a:before
{
    font-size: 6px;
    line-height: 10px;

    position: absolute;
    top: 50%;
    left: 8px;

    display: block;

    width: 10px;
    height: 10px;
    margin-top: -5px;
}

#sidebar-menu li ul li a.sf-with-ul:after
{
    right: 10px;
}

#sidebar-menu li ul li ul li a:before
{
    left: 15px;
}

#sidebar-menu .text-center
{
    padding: 15px;
}

#sidebar-menu .header
{
    font-size: 14px;

    padding: 10px 15px 5px;

    color: rgba(255,255,255,.7);
}

/* Header */

#page-header
{
    position: relative;
    height: 50px;
    padding-left: 235px;
}

#header-right
{
    float: right;

    margin-right: 10px;
}

#header-right .dropdown-menu
{
    padding: 0;
}

#header-right .dropdown-menu.float-right
{
    right: 8px;
}

#header-logo
{
    font-size: 15px;
    line-height: 50px;

    float: left;

    height: 50px;
    margin-right: 10px;
    padding: 0 0 0 15px;
    position: fixed;
    z-index: 7;
    left: 0;
    top: 0;
}

#header-logo i
{
    font-style: normal;
}

#header-logo a
{
    float: right;

    width: 50px;

    text-align: center;
}

.user-profile
{
    float: right;
}

#page-header .dash-menu
{
    float: right;
}

#page-header .dash-menu > .medium.btn
{
    margin-top: 12px;
}

.user-profile > a
{
    line-height: 50px;

    display: block;

    height: 50px;
    margin: 0;
    padding: 0 0 0 15px;
}

.user-profile > a span
{
    font-size: 14px;

    padding: 0 5px;
}

.user-profile > a span,
.user-profile > a .glyph-icon
{
    display: block;
    float: left;
}

.user-profile > a img
{
    float: left;

    width: 34px;
    height: 34px;
    margin: 7px 5px 0 0;

    border-radius: 4px;
}

.user-profile > .dropdown-menu
{
    right: 5px;
}

.top-icon-bar
{
    float: left;
}

.top-icon-bar .dropdown > a
{
    line-height: 44px;

    position: relative;

    display: block;
    float: left;

    width: 44px;
    height: 44px;
    margin: 3px 0 0;

    text-align: center;

    border-radius: 2px;
}

.top-icon-bar .dropdown > a .bs-badge
{
    line-height: 14px;

    top: 2px;
    left: 2px;

    display: block;

    width: 14px;
    min-width: 14px;
    height: 14px;
    padding: 0;
}

.top-icon-bar .dropdown > a .glyph-icon
{
    font-size: 20px;
}

/* Content */

#page-wrapper
{
    position: relative;
}

#page-content-wrapper
{
    position: relative;
    z-index: 6;
    margin-left: 235px;
}

#page-content
{
    padding: 10px;
}

.page-box
{
    min-height: 600px;
    margin: 0 0 5px;
    padding: 25px;
}

.page-box h3.page-title
{
    position: relative;

    margin: -25px -25px 25px;
    padding: 15px 25px 20px;
}

.page-box h3.page-title small
{
    line-height: 1.6em;

    display: block;

    width: 80%;
    padding: 0 0 5px;
    font-size: 15px;

         opacity: .70;

    -moz-opacity: .70;
    filter: alpha(opacity: 70);
}

.page-title + .tabs-navigation
{
    margin: -25px -25px 0;
}

.page-title + .tabs-navigation ul
{
    border-width: 1px 0;
}

/* Breadcrumb */

#page-breadcrumb
{
    font-size: 12px;

    padding: 17px 0 0;

    text-transform: uppercase;
}

#page-breadcrumb a
{
    margin-right: 10px;
}

#page-breadcrumb .glyph-icon
{
    margin-right: 5px;

         opacity: .40;

    -moz-opacity: .40;
    filter: alpha(opacity: 40);
}

#page-breadcrumb a:after
{
    font-family: FontAwesome;
    font-size: 12px;

    margin-left: 5px;

    content: '\f054';
}

#breadcrumb-right
{
    position: absolute;
}

/* Page nav */

#page-nav
{
    position: relative;

    height: 56px;
    padding: 0 10px;
}

#page-nav-right
{
    position: absolute;
    top: 10px;
    right: 5px;
}

#page-nav-right a i.glyph-icon
{
    font-size: 13px;

    margin-right: 5px;
}

.sp-btn
{
    position: absolute !important;
    top: 11px;
    left: 10px;
}

#sidebar-search
{
    position: relative;

    float: left;

    margin: 9px 0 0 10px;
}

#sidebar-search input
{
    width: 100px;
    height: 32px;
    padding-right: 15px;
    padding-left: 29px;
}

#sidebar-search .glyph-icon
{
    position: absolute;
    top: 7px;
    left: 12px;
}

#page-subnav
{
    margin: 0;
    padding: 0;

    text-align: center;
}

#page-subnav.side-opened
{
    width: 65%;
    margin: 0 auto;
}

#page-subnav li
{
    line-height: 30px;

    display: inline-block;

    height: 30px;
}

#page-subnav li a
{
    font-size: 14px;
    line-height: 56px;

    display: block;

    height: 56px;
    margin: 0 12px;
    padding: 0 3px;

    border-bottom: transparent solid 2px;
}

#page-subnav li .glyph-icon
{
    display: none;
}

#page-sidebar .divider
{
    margin: 5px 15px;

    border-bottom: 0;
}


/* Right menu */

.sb-slidebar
{
    padding: 14px;
}

.sb-slidebar .popover-title
{
    font-size: 13px;
    font-size: 11px;
    line-height: 36px;

    display: block;

    height: 36px;
    margin: 2px 0;
    padding: 0 15px;

    border-width: 0;
}

.sb-slidebar .divider
{
    background: rgba(255,255,255,.1);
}

.sb-slidebar.sb-left .divider-header
{
    font-size: 14px;

    opacity: .4;
}

.sb-slidebar .popover-title .caret
{
    float: right;

    margin-top: 17px;
}

.sb-slidebar .progress-box li a
{
    display: block;

    padding: 0;
}

.sb-slidebar .files-box
{
    padding: 10px 0;
}

.sb-slidebar .files-box li.divider
{
    margin: 5px 0;
    padding: 0;
}

.sb-slidebar .notifications-box li,
.sb-slidebar .progress-box li
{
    padding: 15px;

    border-color: rgba(255,255,255,.1);
}

.sb-slidebar .notifications-box li:last-child,
.sb-slidebar .progress-box li:last-child
{
    padding-bottom: 20px;
}

.page-title ~ .divider,
.page-title ~ h2.title-hero {
    display: none;
}

.page-title ~ .title-lead {

}
